<template>
  <div>
    <div class="add">
      <router-link to="/noticeAdd">
        <van-icon name="plus" />
      </router-link>
    </div>

    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-swipe-cell v-for="item in NoticeList" :key="item.id" 
      :before-close="beforeClose" >
        <div class="notice-card" :class="item.isShow?'':'unactive'" @click="update(item.id)">
          <div class="card-left">
            <p>{{getDateYear(item.benginTime)}}</p>
            <p>{{getDateMonth(item.benginTime)}}月</p>
            <p class="day">{{getDateDate(item.benginTime)}}</p>
            <p>周{{getDateDay(item.benginTime)}}</p>
          </div>
          <div class="card-right" >
            <p class="notice-time">提醒时间:{{getDateTime(item.benginTime)}}</p>
            <p class="notice-content">{{item.content}}</p>
            <p class="notice-time">{{getDateTime(item.noticeTime)}}</p>
          </div>
        </div>
        <template #right>
          <van-button
            square
            text="删除"
            type="danger"
            class="delete-button"
          />
        </template>
      </van-swipe-cell>
    </van-pull-refresh>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 下拉刷新的属性
      isLoading: false,
      card: {
        month: 3,
        date: 27,
        day: 5,
        content: '开会202室',
        time: '7:00-14:00'
      },
      NoticeList: [],
      userId: null,
      itemId: null,
      nowClick: null
    }
  },
  created () {
    this.userId = window.sessionStorage.getItem('userId')
    this.getNotice()
  },
  methods: {
    getNotice: async function () {
      // TODO 获取行程
      const { data: res } = await this.$http.get('/my/notices/getNoticesByUserId?userId=' + this.userId);
      if (res.code !== 0) return this.$toast.fail('获取行程失败,' + res.msg)
      this.NoticeList = res.data;
    },
    // 刷新数据
    onRefresh: function () {
      this.getNotice();
      this.$toast('刷新成功');
      this.isLoading = false;
    },
    // 删除行程提醒
    // position 为关闭时点击的位置
    // instance 为对应的 SwipeCell 实例
    beforeClose ({ position, instance }) {
      var self = this;
      switch (position) {
        case 'left':
        case 'cell':
        case 'outside':
          instance.close();
          break;
        case 'right':
          this.$dialog.confirm({
            message: '确定删除吗？'
          }).then(async() => {
            instance.close();
            var id = instance.$options._parentVnode.data.key;
            // on confirm
            // TODO 删除行程

             const {data:res} = await this.$http.delete('/my/notices/deleteById?id='+id);
            if(res.code !== 0) return this.$notify({ type: 'warning', message: '删除失败，请稍后再试' });
            self.$notify({ type: 'success', message: '删除成功'});
            this.getNotice();
          }).catch(() => {
            // on cancel
          });
          break;
      }
    },
    update(id){
      this.$router.push('/noticeUpdate/'+id)
    },
    getDateYear (str) {
      var date = new Date(str);
      return date.getFullYear();
    },
    getDateMonth (str) {
      var date = new Date(str);
      return date.getMonth() + 1
    },
    getDateDate (str) {
      var date = new Date(str);
      return date.getDate()
    },
    getDateDay (str) {
      var date = new Date(str);
      var arr = ['日', '一', '二', '三', '四', '五', '六'];
      return arr[date.getDay()]
    },
    getDateTime (str) {
      var date = new Date(str);
      return `${date.getHours() < 10 ? '0' + date.getHours() : date.getHours()}:${date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()}`;
    },
  },
}
</script>
