  .van-icon {
    color: white;
    font-weight: bolder;
    font-size: 60px;
    border: 5px solid #fff;
    background-color: #ffe957;
    border-radius: 5px;
  }
}

.van-swipe-cell {
  margin-bottom: 10px;
  border-radius: 10px;
}
.delete-button {
  height: 100%;
}
.notice-card {
  position: relative;
  box-sizing: border-box;
  padding: 8px 16px;
  color: #323233;
  font-size: 12px;
  background-color: #fff;
  display: flex;
  flex-direction: row;
  &.unactive {
    .card-left,
    .card-right,
    .notice-time {
      color: #bbb !important;
    }
  }
  .card-left {
    text-align: center;
    color: #f29f3f;

    p {
      font-weight: bold;
      line-height: 24px;
    }
    .day {
      font-size: 22px;
    }
  }
  .card-right {
    text-align: right;
    width: 90%;
    padding-left: 15px;
    display: flex;
    flex-direction: column;
    align-self: center;
    .notice-content {
      font-size: 20px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }
    .notice-time {
      font-size: 15px;
      margin-top: 5px;
      color: #444;
    }
  }
}
</style>